import axios from 'axios';
import React, { useState } from 'react';
import { useForm } from "react-hook-form";
import "./AddProduct.css"

const AddProduct = () => {


    const { register, handleSubmit, watch, formState, errors } = useForm();
    const[imgUrl,setImgUrl]=useState(null);
    const onSubmit = data => {

       const productData ={
           name:data.name,
           price:data.price,
           imgUrl:imgUrl
       };
       const url=`http://localhost:5000/addProduct`;



        console.log(productData)

        fetch(url, {
      method: 'POST', 
      headers: {
        'content-type': 'application/json'
      },
      body: JSON.stringify(productData)
    })
    .then(res => console.log('server side response', res))
  };




const handleImageUpload = event => {
    console.log(event.target.files[0]);
    const imageData = new FormData();
    imageData.set('key','666aeb5b21a24cde55b95df3fc04f111');
    imageData.append('image',event.target.files[0]);
   axios.post('https://api.imgbb.com/1/upload',imageData)
 
 .then(function (response) {
    setImgUrl(response.data.data.display_url);
   })
   .catch(function (error) {
     console.log(error);
   });
 
 }





    return ( 
      <div className="login">

        <div>
       <h1>this is for inventory</h1>



        </div>



    <div id="add-container">
    <h2>Add Product</h2>
    <form onSubmit={handleSubmit(onSubmit)}>
  
    <input name="name" defaultValue="product" {...register('name')} />
    <input name="price" defaultValue="price" {...register('price')} />
    <br/>
    <input name="weight" defaultValue="weight" {...register('weight')} />
    <input id="pic" name="product sample" type="file" onChange={handleImageUpload}/>
    <br/>
    
    <br/>
    <input id="submit" type="submit" />
  </form>
  </div>


  </div>
  );
};

export default AddProduct;